import quijoteluiService from './quijotelui'

const trackService = {}

trackService.buscaFactura = function (fechaInicio, fechaFin, estado) {
  // console.log(quijoteluiService)
  const fechaReporte = `/reporte_factura_estado/fechaInicio/${fechaInicio}/fechaFin/${fechaFin}/estado/${estado}`
  return quijoteluiService.get(fechaReporte)
    .then(res => {
      console.log(`Url Base: ${quijoteluiService._baseUrl}`)
      return res.data
    })
}

trackService.autorizaFactura = function (fechaInicio, fechaFin) {
  const fechasAutorizar = `/factura_autoriza/fechaInicio/${fechaInicio}/fechaFin/${fechaFin}`
  return quijoteluiService.get(fechasAutorizar)
    .then(res => {
      return res.data
    })
}

