trackService.contribuyente = function () {
  return quijoteluiService.get(`/contribuyentes`)
    .then(res => res.data)
}

trackService.pdf = function () {
  const comprobante = `/pdf/`
  return `${quijoteluiService._baseUrl}${comprobante}`
}

