import Menu from '@/components/Menu.vue'
import About from '@/components/About.vue'
import Contacto from '@/components/Contacto.vue'
import Factura from '@/components/Factura.vue'
import Retencion from '@/components/Retencion.vue'
import NotaCredito from '@/components/NotaCredito.vue'
import Guia from '@/components/Guia.vue'
import Empresa from '@/components/Empresa.vue'
import Electronico from '@/components/Electronico.vue'

const routes = [
  { path: '/', component: Menu, name: 'menu' },
  { path: '/factura', component: Factura, name: 'factura' },
  { path: '/retencion', component: Retencion, name: 'retencion' },
  { path: '/notacredito', component: NotaCredito, name: 'notacredito' },
  { path: '/guia', component: Guia, name: 'guia' },
  { path: '/about', component: About, name: 'about' },
  { path: '/contacto', component: Contacto, name: 'contacto' },
  { path: '/empresa', component: Empresa, name: 'empresa' },
  { path: '/electronico/:numero', component: Electronico, name: 'electronico' }
]

export default routes



